import { DraggableDataProps, DropHolderProps } from '../types';

export default function updateDropHolder(draggableData: DraggableDataProps, payload?: DropHolderProps) {
  const { dropHolder, ...rest } = draggableData;

  if (!payload && !dropHolder) {
    // 都为空不更新
    return draggableData;
  }

  if (
    dropHolder?.key === payload?.key &&
    dropHolder?.dropPosition === payload?.dropPosition
  ) {
    // 同样位置不更新
    return draggableData;
  }

  if (!payload) {
    return rest;
  }
  return {
    ...rest,
    dropHolder: payload,
  };
}
